@keyframes slideInFromLeft{
    0%{
        transform:translateX(-20%);
        opacity: 0;
    }
    100%{
        transform:translateX(0);
        opacity: 1;
    }
}



.works{
    display: flex;
    flex: 1;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 25px;
}

.worksContainer{
    flex:0.8;
    display: flex;
    background-color: red;
    
}

.imageWork{
    animation: 1s ease-out 0s 1 slideInFromLeft;
    width: 100%;
    box-shadow: 0px 0px 40px 0 rgb(0,0,0,0.25);
}

.spacing{
    flex:0.1;
}