@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{
  margin:0;
  box-sizing: border-box;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
   /* background-image: url("./Assets/BG1.jpg");
  background-position: center;
  background-size: cover;
  background-attachment: fixed; */
  background-color: #1a191d;
  
   /*
  background: -webkit-linear-gradient(to right, #898add, #fca5ec); 
  background: linear-gradient(to right, #898add, #fca5ec); */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  scroll-behavior: smooth;
}
@-webkit-keyframes slideInFromLeft1{
    0%{
        transform:translateX(-20%);
    }
    100%{
        transform:translateX(0);
    }
}
@keyframes slideInFromLeft1{
    0%{
        transform:translateX(-20%);
    }
    100%{
        transform:translateX(0);
    }
}

.body{
    flex:1 1;
}

.heading{
    font-family: 'Inter';
    font-size: 21px;
    font-weight: 500;
    color: white;
    padding-top: 20px;
    padding-bottom: 5px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
}

.tagline{
    font-family: 'Inter';
    font-size: 30px;
    color: white;
    font-weight: 800;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
}


.product{
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 5px;
    display: grid;
}

.openBadge{
    background-color: #003cd3;
}

.mentr{
    background-color: purple;
}

.productDetails{
    padding-top:30px;
    padding-bottom:30px;
    padding-left:30px;
    padding-right:30px;
    font-family: 'Inter';
    line-height: 1.5rem;
    color: white;
}

.productName{
    margin-bottom: 15px;
}

.productDescription{
    font-weight: 380;
    opacity: 0.7;
    margin-bottom: 15px;
}

.getStarted{
    font-family: 'Inter';
    cursor: pointer;
    border-radius: 3px;
    background-color: #20292f;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 10px;
    color: white;
    border:none;
}

.cards{
    padding-top: 30px;
    padding-bottom: 30px;
    padding-right: 20px;
    padding-left: 20px;
    margin: 0 auto;
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.features{
    border-radius: 3px;
    color: white;
    font-size: 15px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
}

.fetaureDescription{
    font-family: 'Inter';
    font-weight: 380;
    line-height: 1.5rem;
    opacity: 0.7;
    margin-top: 10px;
}

.show{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 30px;
    display: grid;
    grid-row-gap: 30px;
}

.showItem{
    padding-top:30px;
    padding-bottom:30px;
    border-radius: 5px;
    line-height: 1.5rem;
    color: white;
    background-color: #003cd3;
}

.projects{
    background-image: url(/Epoch-Zero-Website/static/media/ProBG.1bb919cb.jpg);
    background-position: center;
    background-size: cover;
    
}

.cap{
    background-image: url(/Epoch-Zero-Website/static/media/Cap1.6a9537ea.jpg);
    background-position: center;
    background-size: cover;
}

.showDetails{
    font-family: 'Inter';
    margin-left: 30px;
    margin-right: 30px;
}

.showName{
    font-size:25px;
    margin-bottom: 15px;
}

.showDescription{
    font-weight: 400;
    opacity: 0.7;
    margin-bottom: 25px;
}

.view{
    font-family: 'Inter';
    cursor: pointer;
    border-radius: 3px;
    background-color: #6f6f6f;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 30px;
    background-color: #003cd3;
    color: white;
    border:none;
}

@media (min-width: 450px) {

.cards { grid-template-columns: repeat(2, 1fr);}
.show { grid-template-columns: repeat(1, 1fr);}
.product { grid-template-columns: repeat(1, 1fr);}
.showItem{
    border-radius: 5px;
}
}

@media (min-width: 800px) {

.cards { grid-template-columns: repeat(3, 1fr); }
.show { grid-template-columns: repeat(2, 1fr);}
.product { grid-template-columns: repeat(2, 1fr);}
.showItem{
    border-radius: 0;
}
}

@media (min-width: 1200px) {
.cards { grid-template-columns: repeat(4, 1fr); }
.heading{
    padding-left: 30px;
    padding-right: 30px;
}
.tagline{
    padding-left: 30px;
    padding-right: 30px;
}
.show{
    margin-left: 120px;
    margin-right: 120px;
    grid-row-gap: 0;
}
.showItem{
    border-radius: 0;
}
.showDetails{
    padding-top:30px;
    padding-bottom:30px;
    font-family: 'Inter';
    margin-left: 50px;
    margin-right: 50px;
}
.product{
    margin-top: 40px;
    margin-left: 120px;
    margin-right: 120px;
}
.productDetails{
    padding-top:60px;
    padding-bottom:60px;
    padding-left:60px;
    padding-right:140px;
}
.cards{
    padding-right: 120px;
    padding-left: 120px;
}
.features{
    background: none;
}
.features:hover{
    background-color: #262738;
}
}

.image{
    -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;
            animation: 1s ease-out 0s 1 slideInFromLeft;
    cursor: pointer;
    width: 100%;
    box-shadow: 0px 0px 20px 0 rgb(0,0,0,0.45);
}

.viewMore{
    cursor: pointer;
    -webkit-animation: 1s ease-out 0s 1 slideInFromLeft1;
            animation: 1s ease-out 0s 1 slideInFromLeft1;
    width: 100%;
    box-shadow: 0px 0px 20px 0 rgb(0,0,0,0.45);
}


.card{
    color: white;
}

.header{
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
    position: -webkit-sticky;
    position: sticky;
    z-index: 200;
    top:0;
    background-color: #1a191d;
}


.nav{
    flex:1 1;
    display: flex;
    color:white;
    font-family: 'Inter';
    font-weight: 900;
    font-size: 20px;
}

.link{
    color: white;
    font-size: 15px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 3px;
}

.logo{
    height: 60px;
    width: 60px;
    border-radius: 100px;
    background-position: center;
    background-size: cover;
    background-color: white;
    box-shadow: 5px 5px 30px 0 rgba(0, 0, 0, 0.2);
}

.app__page{
  display:flex;
  flex:1 1
}

.app{
  display: flex;
  flex-direction: column;
}


@-webkit-keyframes slideInFromLeft{
    0%{
        transform:translateX(-20%);
        opacity: 0;
    }
    100%{
        transform:translateX(0);
        opacity: 1;
    }
}



@keyframes slideInFromLeft{
    0%{
        transform:translateX(-20%);
        opacity: 0;
    }
    100%{
        transform:translateX(0);
        opacity: 1;
    }
}



.works{
    display: flex;
    flex: 1 1;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 25px;
}

.worksContainer{
    flex:0.8 1;
    display: flex;
    background-color: red;
    
}

.imageWork{
    -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;
            animation: 1s ease-out 0s 1 slideInFromLeft;
    width: 100%;
    box-shadow: 0px 0px 40px 0 rgb(0,0,0,0.25);
}

.spacing{
    flex:0.1 1;
}
